import React from 'react';
import PropTypes from 'prop-types';
import { graphql, useStaticQuery } from 'gatsby';
import Url from 'url';

import { HeaderTag, Layout, PostCard, Tag } from '../components/common';
import { WebsiteMeta } from '../components/common/meta';

/**
 * Tag page (/tag/:slug)
 *
 * Loads all posts for the requested tag incl. pagination.
 *
 */
const TagPage = ({ location, tag, title, posts, children }) => {
  const {
    site: {
      siteMetadata: { siteUrl },
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          siteUrl
        }
      }
    }
  `);

  const canonical = Url.resolve(siteUrl, location.pathname);

  return (
    <>
      <WebsiteMeta type="Series" canonical={canonical} title={title} />
      <Layout header={<HeaderTag tag={tag} numberOfPosts={posts.length} />}>
        <div className="inner posts">
          <div className="tag-cta">{children}</div>
          <div className="post-feed">
            {posts.map((p, i) => (
              <PostCard
                key={p.id}
                post={p}
                num={i}
                isHome
                label={<Tag label={'New'} isNew />}
              />
            ))}
          </div>
        </div>
      </Layout>
    </>
  );
};

TagPage.propTypes = {
  posts: PropTypes.arrayOf(
    PropTypes.shape({
      codeinjection_styles: PropTypes.string,
      id: PropTypes.number.isRequired,
      title: PropTypes.string.isRequired,
      html: PropTypes.string.isRequired,
      custom_excerpt: PropTypes.string,
      feature_image: PropTypes.string,
      featured: PropTypes.bool,
      tags: PropTypes.arrayOf(PropTypes.object.isRequired),
      authors: PropTypes.arrayOf(PropTypes.object.isRequired).isRequired,
      primary_tag: PropTypes.shape({
        name: PropTypes.string,
        slug: PropTypes.string,
      }),
      published_at: PropTypes.string.isRequired,
      published_at_pretty: PropTypes.string.isRequired,
      children: PropTypes.arrayOf(PropTypes.object),
      featureImageSharp: PropTypes.object,
    })
  ).isRequired,
  location: PropTypes.object.isRequired,
  title: PropTypes.string.isRequired,
  tag: PropTypes.string.isRequired,
};

export default TagPage;
